import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Response } from "@angular/http";
import { Observable } from 'rxjs';
import 'rxjs/add/operator/map';
import { User } from './user.model';
import { AppSettings } from '../../../app/appSettings.setting';

@Injectable()
export class UserService {
  public static AccountUrl = AppSettings.BaseAPIUrl + 'Account/';
  public static WorkFlowUrl = AppSettings.BaseAPIUrl + 'WorkFlowUser/';
  public static user: [any];
  constructor(private http: HttpClient) { }

  registerUser(user: User) {
    const body: User = {
      UserName: user.UserName,
      Password: user.Password,
      Email: user.Email,
      FirstName: user.FirstName,
      LastName: user.LastName
    }
    var reqHeader = new HttpHeaders({ 'No-Auth': 'True' });
    return this.http.post(UserService.AccountUrl + 'Register', body, { headers: reqHeader });
  }

  userAuthentication(userName, password) {
    var data = "username=" + userName + "&password=" + password + "&grant_type=password";
    var reqHeader = new HttpHeaders({ 'Content-Type': 'application/x-www-urlencoded', 'No-Auth': 'True' });
    return this.http.post('https://ersopportunityapi.azurewebsites.net/token', data, { headers: reqHeader });
  }

  getUserClaims() {
    return this.http.get(UserService.AccountUrl + 'GetUserClaims');
  }

  getUserWorkFlowDataById(userId: string) {
    return this.http.get(UserService.WorkFlowUrl + 'userdetails/' + userId + '/');
  }

  getUserWorkFlowData() {
    return this.http.get(UserService.WorkFlowUrl + 'userdetails');
  }

}



