import { Component, OnInit, TemplateRef, ViewChild } from '@angular/core';
import { CommonModule } from '@angular/common';
import { Router } from '@angular/router';
import { UserService } from './common/user/user.service';
import { User } from "./shared/models/user.model";

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent {
  public user: User[];
  constructor(private router: Router, private userService: UserService) {
  }

  get isUserLogged(): boolean {
    return localStorage.getItem('userToken') != null ? true : false;
  }

  get userFullName(): string {
    let userDetails = localStorage.getItem('userWorkFlow') != null ? JSON.parse(localStorage.getItem('userWorkFlow')) : { firstName: 'Welcome', lastName: 'Guest' };
    let name = userDetails.firstName + ' ' + userDetails.lastName;
    return name;
  }

  ngOnInit() { }

  Logout() {
    localStorage.removeItem('userName');
    localStorage.removeItem('userToken');
    localStorage.removeItem('userWorkFlow')
    this.router.navigate(['/login']);
  }
}



