import { Injectable } from '@angular/core';
import { Headers, Http, RequestOptions, Response } from '@angular/http';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Observable } from 'rxjs/Rx';
import 'rxjs/add/operator/map';
import 'rxjs/add/operator/catch';
import 'rxjs/add/operator/toPromise';

@Injectable()
export class WebApiManager {
  headerObj: any;
  // TODO - Implementing proper logging
  requestOptions: RequestOptions;
  constructor(private http: HttpClient) {
    this.headerObj = new Headers({
      'No-Auth': 'True'
    });
  }

  Post(url: string, dataToPost: any) {
    console.log(`Angular2 - Making HTTP POST -> URL: ${url}, with DATA: ${JSON.stringify(dataToPost)}`);
    return this.http.post(url, dataToPost)
      //.map(this.extractData)
      .catch(this.handleError);
  }

  Get(url: string) {
    return this.http.get(url)
      //.map((res: Response) => res.json())
      //.do(response => console.log('fetched ' + url))
      .catch(this.handleError);
  }

  PostWithNoAuthentication(url: string, dataToPost: any) {
    console.log(`Angular2 - Making HTTP POST -> URL: ${url}, with DATA: ${JSON.stringify(dataToPost)}`);
    var reqHeader = new HttpHeaders({ 'Content-Type': 'application/x-www-urlencoded', 'No-Auth': 'True' });
    return this.http.post(url, dataToPost, { headers: reqHeader })
      //.map(this.extractData)
      .catch(this.handleError);
  }

  GetWithNoAuthentication(url: string) {
    var reqHeader = new HttpHeaders({ 'Content-Type': 'application/x-www-urlencoded', 'No-Auth': 'True' });
    return this.http.get(url, { headers: reqHeader })
      //.map((res: Response) => res.json())
      //.do(response => console.log('fetched ' + url))
      .catch(this.handleError);
  }





  private handleError(error: any): Promise<any> {
    // TODO - Do proper logging
    console.error('An error occurred', error);
    return Promise.reject(error.message || error);
  }

  private extractData(res: Response) {
    let body = res.json();
    return body || {};
  }
}



