import { Component, OnInit } from '@angular/core';
import { UserService } from '../user.service';
import { HttpErrorResponse } from '@angular/common/http';
import { Router, NavigationExtras } from '@angular/router';
import { AuthService } from '../../../shared/services/auth.service';
import { User } from '../../../shared/models/user.model';
import { UserWorkFlow } from '../../../shared/models/userworkflow.model';
import { Ng4LoadingSpinnerService } from 'ng4-loading-spinner';

@Component({
  selector: 'app-sign-in',
  templateUrl: './sign-in.component.html',
  styleUrls: ['./sign-in.component.css']
})
export class SignInComponent implements OnInit {
  isLoginError: boolean = false;
  public loading: boolean = true;
  public template: string = undefined;
  // public template: string = `<img src="http://pa1.narvii.com/5722/2c617cd9674417d272084884b61e4bb7dd5f0b15_hq.gif" />`;
  //public static user: User = new User();
  constructor(private authService: AuthService, private userService: UserService, private router: Router, private spinnerService: Ng4LoadingSpinnerService) { }

  ngOnInit() {
    this.spinnerService.hide();
    if (localStorage.getItem('userToken') != null) {
      let navigationExtras: NavigationExtras = {
        preserveQueryParams: true,
        preserveFragment: true
      };
      this.router.navigate(['opportunitylist'], navigationExtras);
    }
  }

  OnSubmit(userName, password) {
    this.isLoginError = false;
    //this.userService.userAuthentication(userName, password).subscribe((data: any) => {
    //  localStorage.setItem('userToken', data.access_token);
    //  this.router.navigate(['/opportunitylist']);
    //},
    //  (err: HttpErrorResponse) => {
    //    this.isLoginError = true;
    //  });

    this.spinnerService.show();
    this.loading = true;
    this.userService.userAuthentication(userName, password)
      .subscribe((data: any) => {
        localStorage.setItem('userToken', data.access_token);
        this.authService.isLoggedIn = true;
        this.authService.token = data.access_token;
        this.authService.userName = userName;
        let redirect = this.authService.redirectUrl ? this.authService.redirectUrl : 'opportunitylist';
        let navigationExtras: NavigationExtras = {
          preserveQueryParams: true,
          preserveFragment: true
        };

        this.userService.getUserWorkFlowDataById(userName).subscribe((userdata: any) => {
          localStorage.setItem('userWorkFlow', JSON.stringify(userdata));
          this.authService.userWorkFlow = userdata;
          this.spinnerService.hide();
          this.router.navigate(['opportunitylist'], navigationExtras);
        }, err => {
          this.spinnerService.hide();
          this.isLoginError = true;
        });
      },
        err => {
          this.spinnerService.hide();
          this.isLoginError = true;
        });
  }

}



