import { BrowserModule } from '@angular/platform-browser';
import { CommonModule } from '@angular/common';
import { NgModule } from '@angular/core';
import { FormsModule} from '@angular/forms';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import { RouterModule } from '@angular/router'
import { HttpClientModule, HTTP_INTERCEPTORS } from '@angular/common/http';
import { AgGridModule } from "ag-grid-angular";
import { DatepickerModule } from 'ngx-bootstrap';
import { TooltipModule } from 'ngx-bootstrap';
import { Ng4LoadingSpinnerModule } from 'ng4-loading-spinner';
import { LoadingModule } from 'ngx-loading';
import { SharedModule } from './shared/shared.module';

import { AppComponent } from './app.component';
import { ToastrModule } from 'ngx-toastr';
import { UserComponent } from './common/user/user.component';
import { SignInComponent } from './common/user/sign-in/sign-in.component';
import { SignUpComponent } from './common/user/sign-up/sign-up.component';
import { NumericCellComponent } from "./common/controls/components/numericCell/numeric-cell.component";
import { DatePickerComponent } from "./common/controls/components/datePickerCell/date-picker.component";
import { NumberOnlyDirective } from './common/directives/number.directive';
import { appRoutes } from './routes';

@NgModule({
  declarations: [
    AppComponent,
    SignUpComponent,
    UserComponent,
    SignInComponent,
    NumericCellComponent,
    DatePickerComponent,
    NumberOnlyDirective
  ],
  imports: [
    BrowserModule,
    CommonModule,
    FormsModule,
    HttpClientModule,
    ToastrModule.forRoot(),
    BrowserAnimationsModule,
    SharedModule.forRoot(),
    RouterModule.forRoot(appRoutes),
    DatepickerModule.forRoot(),
    TooltipModule.forRoot(),
    Ng4LoadingSpinnerModule.forRoot(),
    LoadingModule,
    AgGridModule.withComponents([NumericCellComponent, DatePickerComponent])
  ],
 
  bootstrap: [AppComponent]
})
export class AppModule { }



