import { Component } from "@angular/core";
import { ICellRendererAngularComp } from "ag-grid-angular";
import * as moment from 'moment';

@Component({
	selector: 'date-picker-cell',
	template: `<i class="fa fa-calendar-o" aria-hidden="true"></i>
        <datepicker (selectionDone)="onClick($event)" ></datepicker>
    `
})
export class DatePickerComponent implements ICellRendererAngularComp {
	private params: any;

	public selectedDate: Date = new Date();
	public userSelectedDate: Date = null;

	agInit(params: any): void {
		this.params = params;
	}

	refresh(params: any): boolean {
		return true;
	}

	getValue(): any {
		//let date = moment(this.selectedDate).format('DD/MM/YYYY')
		//return `${date}`;
		if (this.userSelectedDate) {
			let date = moment(this.userSelectedDate).format('DD/MM/YYYY')
			return `${date}`;
		}
		else {
			//let date = moment(this.selectedDate).format('DD/MM/YYYY')
			//return `${date}`;
			return ` `;
		}
	}

	isPopup(): boolean {
		return true;
	}

	onClick(date: Date) {
		//this.selectedDate = date;
		this.userSelectedDate = date;
		this.params.api.stopEditing();
	}
}



